.solution {
  padding: 80px 60px;
  background: #ffffff;
}

.solution .label {
  font-size: 12px;
  font-weight: 700;
  color: #2563eb;
  letter-spacing: 0.1em;
}

.solution h2 {
  font-size: 32px;
  font-weight: 800;
  margin: 12px 0 40px;
  color: #0f172a;
}

.cards {
  display: flex;
  gap: 24px;
}

.card {
  flex: 1;
  padding: 32px;
  border-radius: 16px;
}

.card svg {
  font-size: 26px;
  margin-bottom: 16px;
}

.card h3 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 8px;
}

.card p {
  font-size: 14px;
  color: #475569;
  line-height: 1.6;
}

.card.blue {
  background: #eef2ff;
  color: #1d4ed8;
}

.card.orange {
  background: #fff7ed;
  color: #c2410c;
}

.card.green {
  background: #ecfdf5;
  color: #047857;
}

