.header {
  display: flex;
  justify-content: space-between;
  padding: 18px 60px;
  align-items: center;
  background: white;
}

.logo {
  font-weight: 700;
  display: flex;
  align-items: center;
}

.logo-dot {
  color: #2563eb;
  margin-right: 8px;
}

nav a {
  margin: 0 16px;
  font-size: 14px;
  cursor: pointer;
}

.download-btn {
  background: #2563eb;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 6px;
  display: flex;
  gap: 8px;
}
