.stats {
  display: flex;
  justify-content: center;
  gap: 24px;
  padding: 40px 20px 80px;
  background: #f8fafc;
}

.stat {
  background: white;
  border-radius: 12px;
  padding: 32px 40px;
  width: 220px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0,0,0,0.04);
}

.stat svg {
  font-size: 28px;
  margin-bottom: 12px;
}

.stat strong {
  display: block;
  font-size: 26px;
  font-weight: 800;
  color: #0f172a;
}

.stat span {
  font-size: 12px;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

