.how {
  background: #f8fafc;
  padding: 80px 60px;
  text-align: center;
}

.how h2 {
  font-size: 28px;
  font-weight: 800;
  color: #0f172a;
}

.how p {
  margin-top: 8px;
  font-size: 14px;
  color: #64748b;
}

.steps {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
  position: relative;
}

.steps::before {
  content: "";
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #e5e7eb;
  z-index: 0;
}

.steps div {
  background: white;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 3px solid #e5e7eb;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 13px;
  z-index: 1;
}

.steps div svg {
  font-size: 24px;
  margin-bottom: 8px;
}

