.audience {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  padding: 80px 60px;
}

.audience-card {
  border-radius: 16px;
  padding: 32px;
}

.audience-card.blue {
  background: #f1f6ff;
}

.audience-card.dark {
  background: #111827;
  color: white;
}

.audience .icon {
  font-size: 28px;
  margin-bottom: 16px;
  color: #2563eb;
}

.audience .icon.dark {
  color: white;
}

.audience ul {
  margin: 24px 0;
}

.audience li {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
}

.outline {
  border: 1px solid #2563eb;
  background: transparent;
  padding: 10px;
  border-radius: 6px;
  color: #2563eb;
}

.filled {
  background: white;
  color: #111827;
  border: none;
  padding: 10px;
  border-radius: 6px;
}
