.footer {
  background: #0b1220;
  color: white;
  padding: 60px;
}

.cols {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
}

.footer a {
  display: block;
  color: #9ca3af;
  margin-bottom: 8px;
}

.bottom {
  margin-top: 40px;
  border-top: 1px solid #1f2937;
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
}
