.donation {
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  color: white;
  text-align: center;
  padding: 100px 20px;
}

.donation-box {
  background: white;
  color: black;
  max-width: 420px;
  margin: 40px auto 0;
  padding: 32px;
  border-radius: 16px;
}

.amounts {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}

.amounts button {
  flex: 1;
  padding: 10px;
}

.amounts .active {
  background: #2563eb;
  color: white;
}

.donate-btn {
  width: 100%;
  background: #f97316;
  color: white;
  border: none;
  padding: 14px;
  border-radius: 8px;
}
