.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 80px 60px;
  background: #ffffff;
}

.hero-left {
  max-width: 520px;
}

.badge {
  display: inline-block;
  background: #eaf2ff;
  color: #2563eb;
  font-size: 12px;
  font-weight: 600;
  padding: 6px 12px;
  border-radius: 999px;
  margin-bottom: 24px;
}

.hero h1 {
  font-size: 44px;
  line-height: 1.2;
  font-weight: 800;
  color: #0f172a;
  margin-bottom: 20px;
}

.hero h1 span {
  color: #2563eb;
}

.hero p {
  font-size: 15px;
  color: #475569;
  line-height: 1.6;
  margin-bottom: 32px;
}

.actions {
  display: flex;
  gap: 16px;
}

.actions button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 14px 18px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
}

.actions .primary {
  background: #2563eb;
  color: white;
  border: none;
}

.actions .secondary {
  background: transparent;
  border: 1px solid #e5e7eb;
  color: #0f172a;
}

.hero-right {
  width: 420px;
  height: 360px;
  border-radius: 16px;
  background-image: url("https://images.unsplash.com/photo-1529156069898-49953e39b3ac");
  background-size: cover;
  background-position: center;
  position: relative;
}

.image-overlay {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  background: linear-gradient(to top, rgba(0,0,0,0.6), transparent);
  color: white;
  font-weight: 600;
  font-size: 14px;
}

